/***** Section pages *****/
.section {
  &-container {
    display: flex;
    justify-content: flex-end;
  }

  &-content {
    @include desktop {
      // flex: 0 0 80%;
      order: 2;
    }
    flex: 1;
  }
  &-aside {
    @include desktop {
      order: 1;
      flex: 0 0 20%;
    }
    // flex: 1;
  }

  &-subscribe {
    .dropdown-toggle::after { display: none; }
  }
}
