.c-cookie-block {
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  padding: $base-spacing-x2;
  background: color($floral, default);
  z-index: 110;
  position: relative;

  p {
    font-size: 0.775rem;
    margin: auto;
    float: left;
    display: inline-table;
  }
}

.c-cookie-block__wrapper {
  display: flex;
}

.c-cookie-block--closed {
  display: none;
  margin-top: -80px;
  transition: all 0.3s ease 0s;
}

.c-cookie-block__btn-close {
  background-image: url(/assets/images/privacy-close.png);
  background-size: cover;
  background-color: transparent;
  width: 17px;
  height: 16px;
  cursor: pointer;
  margin: 12px;
  display: table;
  position: absolute;
  bottom: 0;
  right: 0;
  float: right;
}

@media screen and (break(medium)) {
  .c-cookie-block {

  }
  p {
    /*width: 95%;*/
    display: block;
  }
  .c-cookie-block__btn-close {
    width: 20px;
    height: 20px;
    margin: 16px;
    position: relative;
    display: block;
    float: none;

  }

  .c-cookie-block__wrapper {
    width: 1220px;
    padding: 0 $base-spacing-x2;
    min-width: 915px;
    position: relative;
    display: flex;
    margin: 0 auto;
  }
}
